div{
    width: min-content;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 200px;

}

li a {
    font-family: 'Roboto Mono', monospace;
    display: block;
    color: white;
    padding: 8px 16px;
    text-decoration: none;
}

li a.active {
    background-color: black;
}

li a:hover:not(.active) {
    background-color: #555;
    color: white;
}
.header{
    width: 100%;
    font-family: 'Roboto Mono', monospace;
}
span{
    text-align: center;
   padding-left: 300px;
}